    right: 20px;
    bottom: 100px; 
    width: 58px;
    height: 58px;
    background: url(data:image/gif;base64,) 0 0;
}
.backtotop:hover {
    background-position: -58px 0;
}
.notice {
    position: fixed;
    z-index: 1000;
    top: 0;
    left: 0;
    overflow: hidden;
    width: 100%;
    height: 50px;
    line-height: 50px;
    font-size: 12px;
    text-align: center;
    background-color: #f57b7b;
}
.header {
    position: fixed;
    z-index: 1000;
    top: 0;
    left: 0;
    overflow: hidden; 
    width: 100%;
    height: 50px;
    border-bottom: 1px solid #e7e7e7;
    background-color: #f8f8f8;
}
.header-brand {
    line-height: 50px;
