    height: 50px;
    margin: 0 6px;
}
.header-brand a {
    font-size: 20px; 
    color: #777;
}
.header-search {
    font-size: 14px;
    overflow: hidden; 
    margin-right: 6px;
    height: 34px;
}
.header-search input {
    height: 20px;
    padding: 6px;
    color: #555;
    max-width: 300px;
    border: 1px solid #ccc;
    border-radius: 4px;
    outline: none; 
    background-color: #fff;
}
.header-search button {
    height: 34px;
    margin-left: 5px;
    padding: 0 15px;
    border: 1px solid #ccc;
    border-radius: 4px;
    background-color: #fff;
}
.header-search button:hover {
    background-color: #f0f0f0;
}
.header-right {
    line-height: 50px;
    height: 50px;
}
.header-right a {
    font-size: 15px;
    padding: 0 6px; 
    display: block;
    color: #777;
}
.footer {
    width: 100%;
    padding: 10px 0;
    color: #333; 
    border-top: 1px solid #ccc;
    background-color: #f8f8f8;
}
.footer p {
    font-size: 13px;
    line-height: 25px;
    height: 25px;
