    text-align: center;
}
.footer p a {
    padding: 0 10px;
    color: #333;
}
.footer p a:hover {
    color: #09f;
}
/*============================ index ============================*/
.index-bg {
    position: fixed; 
    width: 100%;
    height: 100%;
    background-color: #bbb;
    background:#333 url(/static/img/bg.c6c0413.jpg) no-repeat center center;
}
.index-header {
    position: fixed;
    z-index: 1; 
    top: 0;
    left: 0;
    width: 100%;
    height: 50px;
}
.index-header-content {
    position: absolute;
    z-index: 2;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0.4; 
    background-color: rgba(0,0,0,0.7);

}
.index-header-content {
    z-index: 3;
    overflow: hidden;
}
.index-header a {
    font-size: 16px;
    line-height: 50px;
    padding: 0 15px; 
    color: #fff;
}
.index-header a.title {
    font-size: 20px;
    font-weight: bold;
    float: left;
}
.index-header a.right {
    float: right;
}
.index-main {
    position: absolute;
    top: 30%;
    left: 50%;
    padding: 0 24px;
    width: 100%;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    height: 160px;
    max-width: 600px;
    transform: translateX(-50%);
    -ms-transform: translateX(-50%);       /* IE 9 */
    -webkit-transform: translateX(-50%);   /* Safari and Chrome */
    -o-transform: translateX(-50%);        /* Opera */
    -moz-transform: translateX(-50%);      /* Firefox */
}
.index-main h1 {
    overflow: hidden;
    height: 60px;
    font-size: 0;
    background: url(/static/img/logo.c0b5966.png) no-repeat center;
    background-size: contain;
    margin: 24px 0;
}
.index-search {
    width: 100%;
    height: 50px;
    text-align: center;
}
.index-search input {
    font-size: 18px;
    line-height: 28px;
    height: 50px;
    padding: 0 10px;
    border: none;
    outline: none;
}
.index-search button {
    font-size: 20px;
    line-height: 50px;
    width: 100px;
    height: 50px;
    padding: 0;
    cursor: pointer;
    text-align: center;
    color: #fff;
    border: none;
    background-color: #00b7ff;
}
.index-foot {
    position: absolute;
    z-index: 1;
    bottom: 10px; 
    left: 0;
    overflow: hidden;
    width: 100%;
    height: 50px;
}
.index-foot p {
    font-size: 13px;
    line-height: 25px;
    height: 25px;
    margin: 0; 
    text-align: center;
    color: #fff;
}
.index-foot p a {
    padding: 0 10px;
    color: #f0f0f0;
}
