.list {
    overflow: hidden; 
    width: 100%;
    margin: 0 0 10px 0;
    padding: 0 12px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
.list li {
    list-style: none;
}
.list li{
    margin: 0;
    padding: 0 0 12px;
    border-bottom: 1px solid #ccc;
}
.list li:first-child {
    border-top: 1px solid #ccc;
}
.list li a{
    display: block;
}
.list li p a:visited .name {
    color: #609;
}
.list li p a:hover .name{
    text-decoration: underline;
}
.list li .name{
    font-size: 16px;
    color: #1a0dab;
    overflow: hidden; 
    white-space: nowrap; 
    text-overflow: ellipsis;
    margin: 12px 0;
}
.list li .info div{
    font-size: 12px; 
    color: #333;
}
.list-declare {
    font-size: 12px;
    line-height: 1.5;
    width: 270px;
    margin-bottom: 5px; 
    padding: 15px;
}
.list-info {
    font-size: 12px;
    color: #999;
    padding: 12px;
    margin: 0;
}
.list-tip {
    font-size: 12px;
    padding-bottom: 10px;
    color: #333;
}
